import { Instance, SnapshotIn, types } from 'mobx-state-tree'
import { VError } from 'verror'

import { GResolveLocation } from '../graph/types'
import { formatAddress } from '../helpers/formats'
import { GeoPoint, gpsToLatLng } from '../helpers/gps'
import { i18n } from '../i18n'
import { BaseModel } from '../models/BaseModel'
import { ResolveLocationQuery } from './LocationModel.graphql'

interface IVolatileProps {
  branchId: MaybeID
  isOpen: boolean
  isResolved: boolean
  isStreetAddress: boolean
  inDeliveryLocation: boolean
  geolocationDisabled: boolean
  geolocationPoint: Maybe<GeoPoint>
  isResolving: boolean
}

export const LocationModel = BaseModel.named('Location')
  .props({
    latitude: types.maybe(types.number),
    longitude: types.maybe(types.number),
    searchValue: types.maybe(types.string),
    address: types.maybe(types.string),
  })
  .volatile<IVolatileProps>(self => ({
    branchId: null,
    isOpen: true,
    isResolved: false,
    isStreetAddress: false,
    inDeliveryLocation: false,
    geolocationDisabled: false,
    geolocationPoint: null,
    isResolving: false,
  }))
  .views(self => ({
    get isGeolocationAvailable() {
      return Boolean(self.env.geolocation && !self.geolocationDisabled)
    },
    get hasLocation() {
      return self.latitude !== undefined || self.longitude !== undefined
    },
    get hasAddress() {
      return Boolean(self.address)
    },
    get isDeliveryAddress() {
      return self.isResolved && self.isStreetAddress && self.inDeliveryLocation
    },
    get hasFinishedSearch() {
      return (
        ((self.searchValue && self.searchValue.length > 0) || self.address) &&
        !self.isResolving
      )
    },
    get geoPoint() {
      if (this.hasLocation) {
        return {
          latitude: self.latitude!,
          longitude: self.longitude!,
        }
      }
      return null
    },
    get resolutionTarget() {
      const { business } = self.root
      if (business.isBrand) {
        return {
          brandId: business.brandId!,
        }
      }
      return {
        branchId: business.branchId!,
      }
    },
    get latLng() {
      if (this.geoPoint) {
        return gpsToLatLng(this.geoPoint)
      }
      return null
    },
  }))
  .actions(self => ({
    setLocation({ latitude, longitude }: any) {
      self.latitude = latitude
      self.longitude = longitude
    },
  }))
  .actions(self => ({
    setResolutionResult(result: GResolveLocation.ResolveLocation) {
      const { finalAddress: address } = result
      if (address) {
        self.address = formatAddress(address)
      }
      self.branchId = result.companyBranch ? result.companyBranch.id : null
      self.isResolved = result.isResolved
      self.isStreetAddress = result.isValidAddress
      self.inDeliveryLocation = result.inDeliveryLocation
      if (result.isResolved) {
        self.setLocation(result.gpsCoordinates)
        self.log('resolved as %s', self.address)
      } else {
        self.log('not resolved')
      }
    },
    setVolatileProps(props: Partial<IVolatileProps>) {
      Object.assign(self, props)
    },
    disableGeolocation() {
      self.geolocationDisabled = false
    },
    clearAddress() {
      self.address = ''
    },
    setIsResolving(state: boolean) {
      self.isResolving = state
    },
  }))
  .actions(self => {
    const resolver = async (vars?: Partial<GResolveLocation.Variables>) => {
      self.setIsResolving(true)

      const variables: GResolveLocation.Variables = {
        ...self.resolutionTarget,
        ...vars,
      }
      const { data } = await self.client.query<GResolveLocation.Query>({
        query: ResolveLocationQuery,
        variables,
      })
      if (data !== null) {
        self.setResolutionResult(data.resolveLocation)
      }
      self.setIsResolving(false)
    }
    return {
      validateUserLocation() {
        if (self.hasLocation) {
          self.log(
            'validating stored user location %s, %s',
            self.latitude,
            self.longitude,
          )
          resolver({ latitude: self.latitude, longitude: self.longitude })
        }
      },
      withUserAddress(address: string) {
        self.searchValue = address
        if (address === '') {
          self.clearAddress()
        } else {
          self.log('resolving user address: %s', address)
          resolver({ address })
        }
      },
      withUserLocation(userLocation: GeoPoint) {
        self.log('with user location: %j', userLocation)
        self.latitude = userLocation.latitude
        self.longitude = userLocation.longitude
        resolver(userLocation)
      },
      useGeolocationResult(point: GeoPoint) {
        self.geolocationPoint = point
      },
      attemptGeolocation() {
        self.log('geolocating...')
        self.env.geolocation.getCurrentPosition(
          ({ coords }) => {
            const { latitude, longitude } = coords
            self.log(
              'geolocated at coords %s, %s (acc: %s)',
              coords.latitude,
              coords.longitude,
              coords.accuracy,
            )
            this.useGeolocationResult({ latitude, longitude })
          },
          err => {
            self.log(new VError('failed geolocation', err))
            self.disableGeolocation()
            window.alert(i18n.t`Your browser does not support geolocation.`)
          },
          {
            enableHighAccuracy: true,
            maximumAge: 60 * 1000,
          },
        )
      },
    }
  })

export interface TLocationModel extends Instance<typeof LocationModel> {}
export interface TLocationModelProps extends SnapshotIn<typeof LocationModel> {}
