import React from 'react'

import { SecondaryButton } from '../common/Buttons'
import { useRoot } from '../common/WithRoot'

interface IProps {}

export const ResetPersistenceButton: React.FC<IProps> = props => {
  const root = useRoot()
  if (process.env.REACT_APP_STAGE === 'prod') {
    return null
  }
  return (
    <SecondaryButton
      onClick={() => {
        root.reset()
        location.reload()
      }}
    >
      Reset uiv. dat [DEV]
    </SecondaryButton>
  )
}
