import gql from 'graphql-tag'

export const JsonAddressFragment = gql`
  fragment GJsonAddress on JsonAddress {
    street
    houseNumber
    city
    state
  }
`

export const AddressFragment = gql`
  fragment GAddress on Address {
    id
    location {
      ...GJsonAddress
    }
  }
  ${JsonAddressFragment}
`

export const GeoPointFragment = gql`
  fragment GGeoPoint on GeoPoint {
    latitude
    longitude
  }
`

export const PriceFragment = gql`
  fragment GPrice on PriceConverted {
    value
    formattedValue
  }
`

export const RecipeFragment = gql`
  fragment GRecipe on Recipe {
    id
    nameLabel
    sideDishCount: sideDishesWareCategoriesCount
  }
`

export const CartItemFragment = gql`
  fragment GCartItem on OrderRecipe {
    id
    automatic
    recipe {
      ...GRecipe
    }
  }
  ${RecipeFragment}
`

export const CompanyBranchStatusFragment = gql`
  fragment GCompanyBranchStatus on CompanyBranch {
    isOpen
    willOpenAt
    willCloseAt
    businessHours {
      id
      openAt
      closeAt
      closed
      closedNote
    }
  }
`

export const PromoCodeFragment = gql`
  fragment GPromoCode on PromoCode {
    id
    code
  }
`
