import React from 'react'
import DocumentTitle from 'react-document-title'

import { appConfig } from '../config'
import { WithRoot } from './WithRoot'

interface IProps {}

export const PageTitle: React.SFC<IProps> = props => (
  <WithRoot
    observe={true}
    render={root => {
      if (root.business.isValid) {
        return (
          <DocumentTitle
            title={`${root.business.branch!.brand.name} - ${
              appConfig.pageTitleSuffix
            }`}
          />
        )
      }
      return <DocumentTitle title={appConfig.pageTitleSuffix} />
    }}
  />
)
