import React from 'react'

import { reportRenderingError } from '../helpers/sentry'

interface IProps {
  prod: boolean
}

export const ErrorContainer: React.SFC<IProps> = ({
  children,
  prod = false,
}) => {
  let ErrorBoundaryContainer

  if (prod) {
    ErrorBoundaryContainer = require('react-error-guard')
      .ProductionErrorBoundary
    return (
      <ErrorBoundaryContainer
        dispatchErrorReporting={({ error, info }) => {
          reportRenderingError(error, info)
        }}
      >
        {children}
      </ErrorBoundaryContainer>
    )
  } else {
    ErrorBoundaryContainer = require('react-error-guard').DeveloperErrorBoundary
    return <ErrorBoundaryContainer>{children}</ErrorBoundaryContainer>
  }
}
