import { DocumentNode } from 'graphql'
import React from 'react'
import { Mutation, MutationProps, MutationResult } from 'react-apollo'

export function buildMutation<TData, TVariables>(mutation: DocumentNode) {
  interface IProps
    extends Omit<MutationProps<TData, TVariables>, 'mutation' | 'children'> {
    render(data: MutationResult<TData>): ReactNode
  }
  const GMutation: React.SFC<IProps> = () => {
    const { render, ...props } = this.props
    return (
      <Mutation mutation={mutation} {...props}>
        {render}
      </Mutation>
    )
  }
  return GMutation
}
