import localforage from 'localforage'
import {
  applySnapshot,
  getSnapshot,
  getType,
  IStateTreeNode,
  onSnapshot,
} from 'mobx-state-tree'

import { appConfig } from '../config'
import { logPersist } from './logger'

const { persistenceKey } = appConfig

export async function persistModel(model: IStateTreeNode) {
  const modelName = getType(model).name
  const stateKey = `${persistenceKey}_${modelName}`

  const writeState = snapshot => localforage.setItem(stateKey, snapshot)

  try {
    const state = await localforage.getItem(stateKey)
    if (state !== null) {
      applySnapshot(model, state)
      logPersist('restored state %s', stateKey)
    } else {
      await writeState(getSnapshot(model))
      logPersist('initialized state %s', stateKey)
    }
  } catch (err) {
    logPersist('persistence failed', err)
    localforage.removeItem(stateKey)
  }

  onSnapshot(model, writeState)
}
