import React from 'react'
import ReactResponsive from 'react-responsive'
import { css } from 'styled-components'

export const Desktop = props => <ReactResponsive {...props} minWidth={768} />
export const Mobile = props => (
  <ReactResponsive {...props} maxAspectRatio="14/9" maxWidth={767} />
)
export const MobileLandscape = props => (
  <ReactResponsive
    {...props}
    screen={true}
    minAspectRatio="14/9"
    maxWidth={767}
  />
)

interface IResponsiveProps {
  desktop: string
  mobile: string
}

export const Responsive: React.SFC<IResponsiveProps> = ({
  children,
  mobile,
  desktop,
}) => (
  <>
    <Desktop className={desktop} children={children} />
    <Mobile className={mobile} children={children} />
  </>
)

export const mediaDesktop = (...args) => css`
  @media (min-width: ${768 / 16}em) {
    // @ts-ignore
    ${css(...args)};
  }
`

export const mediaMobile = (...args) => css`
  @media (max-width: ${767 / 16}em) {
    // @ts-ignore
    ${css(...args)};
  }
`
