import debug from 'debug'
import { getEnv, getPath, getRoot, getType, types } from 'mobx-state-tree'

import { IRootEnvironment, TRootModel } from './RootModel'

export const BaseModel = types.model().views(self => ({
  get log() {
    const name = getPath(self).trim() || getType(self).name
    return debug(`c2e:model/${name}`)
  },
  get env() {
    return getEnv<IRootEnvironment>(self)
  },
  get client() {
    const { client } = getEnv<IRootEnvironment>(self)
    return client
  },
  get root() {
    return getRoot<TRootModel>(self)
  },
}))
