import { setContext } from 'apollo-link-context'
import { Instance, SnapshotIn, types } from 'mobx-state-tree'

import { appConfig } from '../config'
import { logAuth } from '../helpers/logger'
import { identifyGuest } from '../helpers/sentry'
import { BaseModel } from './BaseModel'

export const AuthModel = BaseModel.named('Auth')
  .props({
    token: types.maybeNull(types.string),
    userId: types.maybeNull(types.string),
  })
  .views(self => ({
    get isValid() {
      return self.token !== null
    },
    getClientHeaders() {
      return {
        Authorization: `Bearer ${self.token}`,
      }
    },
  }))
  .actions(self => ({
    authenticate(token: string, userId: string) {
      self.token = token
      self.userId = userId
      identifyGuest(userId, token)
    },
  }))
  .actions(self => {
    let fetching

    const fetch = async () => {
      if (!fetching) {
        fetching = fetchToken()
        const { token, userId } = await fetching
        self.authenticate(token, String(userId))
        fetching = null
      }
      return fetching
    }

    const authLink = setContext(async () => {
      if (!self.token) {
        await fetch()
      }
      return { headers: self.getClientHeaders() }
    })

    return {
      attachAuthLinkToClient() {
        self.client.link = authLink.concat(self.client.link)
      },
    }
  })
  .preProcessSnapshot(snapshot => {
    if (snapshot.userId && snapshot.token) {
      identifyGuest(snapshot.userId, snapshot.token)
    }
    return snapshot
  })

function fetchToken() {
  const { endpoint } = appConfig
  if (!endpoint) {
    throw new Error('Missing endpoint in configuration.')
  }
  logAuth('retrieving guest token...')
  const url = `${endpoint}/users/token?guest=true`
  const options = {
    headers: {
      Accept: 'application/json',
    },
  }
  logAuth('token retrieved')
  return fetch(url, options).then(response => response.json())
}

export interface TAuthModel extends Instance<typeof AuthModel> {}
export interface TAuthModelProps extends SnapshotIn<typeof AuthModel> {}
