import Color from 'color'
import { Instance, SnapshotIn, types } from 'mobx-state-tree'

import { GCompanyBranchStyle } from '../graph/types'
import { BaseModel } from './BaseModel'
import { CompanyBranchStyleQuery } from './ThemeModel.graphql'

// Fallback colors
const defaultPrimary = '#54984a'
const defaultSecondary = '#99230e'
const defaultTertiary = '#d9a700'

export const ThemeModel = BaseModel.named('Theme')
  .props({
    storeLogo: types.maybeNull(types.string),
    storeLogoMobile: types.maybeNull(types.string),
    backgroundCart: types.maybeNull(types.string),
    primary: types.maybeNull(types.string),
    secondary: types.maybeNull(types.string),
    secondaryHover: types.maybeNull(types.string),
    tertiary: types.maybeNull(types.string),
    tertiaryHover: types.maybeNull(types.string),
    tertiaryText: 'white',
    unselected: '#E5E5E5',
    unselectedHover: '#EDEDED',
    unselectedText: '#5A5A5A',
    loaded: false,
  })
  .actions(self => ({
    applyStyle(styles: GCompanyBranchStyle.Styles) {
      Object.assign(self, {
        primary: styles.active ? styles.active : defaultPrimary,
        secondary: styles.main ? styles.main : defaultSecondary,
        secondaryHover: calcHoverColor(
          styles.main ? styles.main : defaultSecondary,
        ),
        tertiary: styles.mainSecondary ? styles.mainSecondary : defaultTertiary,
        tertiaryHover: calcHoverColor(
          styles.mainSecondary ? styles.mainSecondary : defaultTertiary,
        ),
        tertiaryText: 'white',
        unselected: '#E5E5E5',
        unselectedHover: '#EDEDED',
        unselectedText: '#5A5A5A',
        storeLogo: styles.storeLogo,
        storeLogoMobile: styles.storeLogoMobile,
        backgroundCart: styles.backgroundCart,
      })
      self.loaded = true
    },
  }))
  .actions(self => ({
    queryStyles: async (branchId: ID) => {
      const { data } = await self.client.query<
        GCompanyBranchStyle.Query,
        GCompanyBranchStyle.Variables
      >({
        query: CompanyBranchStyleQuery,
        variables: { branchId },
      })
      self.applyStyle(data.companyBranch.styles)
    },
  }))

function calcHoverColor(inputColor: string) {
  return Color(inputColor)
    .lighten(0.2)
    .hsl()
    .string()
}

export interface TThemeModel extends Instance<typeof ThemeModel> {}
export interface TThemeModelProps extends SnapshotIn<typeof ThemeModel> {}
