import { Instance, SnapshotIn, types } from 'mobx-state-tree'
import GoogleMap from 'react-google-maps/lib/components/GoogleMap'

import { GeoPoint, gpsToLatLng, latLngToGps } from '../helpers/gps'
import { BaseModel } from '../models/BaseModel'

interface IVolatileProps {
  mapRef: Maybe<GoogleMap>
  clickPoint: Maybe<GeoPoint>
}

export const GPSCzechia = {
  latitude: 49.8037633,
  longitude: 15.4749126,
}

export const MapModel = BaseModel.named('Map')
  .props({
    zoom: types.optional(types.number, 16),
    latitude: types.optional(types.number, GPSCzechia.latitude),
    longitude: types.optional(types.number, GPSCzechia.longitude),
  })
  .volatile<IVolatileProps>(self => ({
    mapRef: null,
    clickPoint: null,
  }))
  .views(self => ({
    get isReady() {
      return self.mapRef !== null
    },
    get center() {
      return gpsToLatLng(self)
    },
  }))
  .actions(self => ({
    setMapRef(ref: Maybe<GoogleMap>) {
      self.mapRef = ref
    },
    updateZoom() {
      if (self.mapRef) {
        self.zoom = self.mapRef.getZoom()
      }
    },
    updatePosition() {
      if (self.mapRef) {
        const center = self.mapRef.getCenter()
        self.latitude = center.lat()
        self.longitude = center.lng()
      }
    },
    panToLocation(gps: GeoPoint, radiusMeters: number = 2000) {
      if (self.mapRef) {
        // const bounds = getBounds([
        //   computeDestinationPoint(gps, radiusMeters, 135), // SE corner
        //   computeDestinationPoint(gps, radiusMeters, 315), // NW corner
        // ])

        // // @ts-ignore padding missing in types
        // self.mapRef.panToBounds(
        //   {
        //     west: bounds.minLng,
        //     east: bounds.maxLng,
        //     north: bounds.minLat,
        //     south: bounds.maxLat,
        //   },
        //   20,
        // )
        self.mapRef.panTo(gpsToLatLng(gps))
      }
    },
    onClick(ev: google.maps.MouseEvent) {
      self.clickPoint = latLngToGps(ev.latLng)
    },
    clearClickPoint() {
      self.clickPoint = null
    },
  }))

export interface TMapModel extends Instance<typeof MapModel> {}
export interface TMapModelProps extends SnapshotIn<typeof MapModel> {}
