import { addHours } from 'date-fns'
import { when } from 'mobx'
import { Instance, SnapshotIn, types } from 'mobx-state-tree'
import { FetchResult } from 'react-apollo'
import { VError } from 'verror'

import { RecipeFragment } from '../graph/fragments.graphql'
import {
  DeliveryTypeEnum,
  GAddToOrder,
  GChangeOrderItem,
  GCreateDeliveryOrder,
  GCreateOrder,
  GCreatePickupOrder,
  GOrder,
  GOrderMobile,
  GOrderPaymentStatus,
  GRecipe,
  GRemoveOrderItem,
  GSubmitOrder,
  GVamStatus,
  PaymentGateEnum,
} from '../graph/types'
import { executeGoPayPayment } from '../helpers/gopay'
import { logMain, logOrder } from '../helpers/logger'
import { RM } from '../helpers/ramda'
import { reportError } from '../helpers/sentry'
import { i18n } from '../i18n'
import { OrderFragment } from '../segments/cart/CartView.graphql'
import { FoodMenuQuery } from '../segments/foodMenu/FoodMenuView.graphql'
import {
  PickupMapBranchQuery,
  PickupMapBrandQuery,
} from '../segments/map/PickupMap.graphql'
import { OrderMobileFragment } from '../segments/mobile/MobileCart.graphql'
import { IOrderFormState } from '../segments/orderForm/OrderFormSetup'
import { BaseModel } from './BaseModel'
import {
  AddToOrderMobileMutation,
  AddToOrderMutation,
  ChangeOrderItemMutation,
  CreateDeliveryOrderMutation,
  CreatePickupOrderMutation,
  OrderCartForRecipeQuery,
  OrderPaymentStatusQuery,
  RemoveOrderItemMutation,
  SubmitOrderMutation,
  VamStatusQuery,
} from './OrderModel.graphql'

interface IVolatileProps {
  isSubmitting: boolean
  isGoPayChecking: boolean
  submitErrors: Maybe<ReadonlyArray<string>>
  agreements: TAgreement[]
  lastActiveRecipeId: MaybeID
  isVamInitialized: boolean
}

export type TAgreement = GVamStatus.VamStatus

export const OrderModel = BaseModel.named('Order')
  .props({
    branchId: types.maybeNull(types.string),
    orderId: types.maybeNull(types.string),
    orderMethod: DeliveryTypeEnum.Messenger,
    payment: PaymentGateEnum.Gopay,
    firstname: '',
    lastname: '',
    email: '',
    phone: '',
    phonePrefix: '',
    note: '',
  })
  .volatile<IVolatileProps>(self => ({
    isSubmitting: false,
    isGoPayChecking: false,
    submitErrors: null,
    agreements: [],
    lastActiveRecipeId: null,
    isVamInitialized: false,
  }))
  .views(self => ({
    get hasOrder() {
      return self.orderId !== null
    },
    get fragmentOrderId() {
      return `Order-${self.orderId}`
    },
    get usingDeliveryOrderMethod() {
      return self.orderMethod === DeliveryTypeEnum.Messenger
    },
    get usingPickupOrderMethod() {
      return self.orderMethod === DeliveryTypeEnum.Pickup
    },
    get hasErrors() {
      return Boolean(self.submitErrors && self.submitErrors.length > 0)
    },
    get defaultPhonePrefix() {
      if (self.root.business.isValid && self.root.business.country) {
        return self.root.business.country.phonePrefix
      }
      return '+'
    },
    getSubmitVariables(formValues: IOrderFormState): GSubmitOrder.Variables {
      return {
        orderId: self.orderId!,
        customer: {
          firstName: formValues.firstname,
          lastName: formValues.lastname,
          emails: [{ email: formValues.email }],
          phones: [{ phone: `${formValues.phonePrefix}${formValues.phone}` }],
        },
        note: formValues.note,
        paymentGate: formValues.payment,
        deliverAt: addHours(new Date(), 1).toISOString(),
        agreements: formValues.agreements
          .filter(RM.propEq('isConfirmed', true))
          .map(RM.prop('id')),
      }
    },
  }))
  .actions(self => ({
    useOrderMethod(method: DeliveryTypeEnum) {
      self.orderMethod = method
    },
    setBranch(branchId: ID) {
      self.branchId = branchId
    },
    orderCreated(result: GCreateOrder.Fragment) {
      self.orderId = result.id
      self.branchId = result.companyBranch.id
      logMain('order %s created for branch %s', self.orderId, self.branchId)
    },
    orderPaid(orderId: ID) {
      if (self.orderId !== orderId) {
        self.orderId = orderId
        logMain('order %s replaced by %s due to successful payment status')
      }
    },
    orderCancel() {
      if (self.branchId || self.orderId) {
        logMain(
          'cancel order %s, was at branch %s',
          self.root.order.orderId,
          self.root.order.branchId,
        )
      }
      self.orderId = null
      self.branchId = null
    },
    submitFinished() {
      self.isSubmitting = false
    },
    submitFailed(errors?: ReadonlyArray<string>) {
      self.isSubmitting = false
      self.submitErrors = errors || [
        i18n.t`Error during sending order. Please try again later.`,
      ]
    },
    vamFailed() {
      self.submitErrors = [
        i18n.t`Internal error during sending order. Order can not be finished right now. Our team was notified and is working on fix. Thank you for your understanding.`,
      ]
      self.isVamInitialized = false
    },
    clearErrors() {
      self.submitErrors = null
    },
    goPayStatusCheck(checking: boolean) {
      self.isGoPayChecking = checking
    },
    useAgreements(agreements: ReadonlyArray<TAgreement>) {
      if (Array.isArray(agreements)) {
        self.agreements = agreements
      }
      self.isVamInitialized = true
    },
    setUsedRecipeId(recipeId: ID) {
      self.lastActiveRecipeId = recipeId
    },
    async queryPaymentStatus(
      orderId: ID = self.orderId!,
    ): Promise<GOrderPaymentStatus.Status | null> {
      const variables: GOrderPaymentStatus.Variables = {
        orderId,
      }
      try {
        const result = await self.client.query<GOrderPaymentStatus.Query>({
          fetchPolicy: 'no-cache',
          query: OrderPaymentStatusQuery,
          variables,
        })
        return result.data.status
      } catch (err) {
        logOrder('failed to verify payment status for order %s: ', orderId)
        return null
      }
    },
    rememberFormValues(values: IOrderFormState) {
      if (self.root.user.shouldRememberForm) {
        self.firstname = values.firstname
        self.lastname = values.lastname
        self.email = values.email
        self.phone = values.phone
        self.phonePrefix = values.phonePrefix
        self.note = values.note
      } else {
        self.firstname = ''
        self.lastname = ''
        self.email = ''
        self.phone = ''
        self.phonePrefix = ''
        self.note = ''
      }
    },
  }))
  .actions(self => ({
    async createDeliveryOrder() {
      const { business, location } = self.root
      if (!location.geoPoint) {
        return null
      }
      const variables: GCreateDeliveryOrder.Variables = {
        gps: location.geoPoint,
        orderOrigin: business.orderOrigin,
        brandId: business.isBrand ? business.brandId : undefined,
        branchId: business.isBrand ? undefined : business.branchId,
      }
      const { data } = await self.client.mutate({
        fetchPolicy: 'no-cache',
        mutation: CreateDeliveryOrderMutation,
        variables,
      })
      const { order } = data as GCreateDeliveryOrder.Mutation
      self.orderCreated(order)
      // @ts-ignore method defined lower
      self.initVam()
      return self.orderId
    },
    async createPickupOrder(branchId: ID) {
      const { business } = self.root
      const variables: GCreatePickupOrder.Variables = {
        branchId,
        orderOrigin: business.orderOrigin,
      }
      const { data } = await self.client.mutate({
        fetchPolicy: 'no-cache',
        mutation: CreatePickupOrderMutation,
        variables,
      })
      const { order } = data as GCreatePickupOrder.Mutation
      self.orderCreated(order)
      // @ts-ignore method defined lower
      self.initVam()
      return self.orderId
    },
    prefetchPickup() {
      const { business } = self.root
      if (business.isBrand) {
        self.client.query({
          query: PickupMapBrandQuery,
          variables: { brandId: business.brandId },
        })
      } else {
        self.client.query({
          query: PickupMapBranchQuery,
          variables: { branchId: business.branchId },
        })
      }
    },
    prefetchFoodMenu(branchId?: ID) {
      if (branchId) {
        self.client.query({ query: FoodMenuQuery, variables: { branchId } })
      }
    },
    prefetchFood(recipeId: ID) {
      self.client.query({
        query: OrderCartForRecipeQuery,
        variables: { recipeId },
      })
    },
    async addFromRecipe(recipeId: ID, sideDishes: Array<ID> = []) {
      const variables: GAddToOrder.Variables = {
        orderId: self.orderId!,
        recipeId,
        sideDishes,
      }
      const current = self.client.readFragment<GOrder.Fragment>({
        id: self.fragmentOrderId,
        fragment: OrderFragment,
        fragmentName: 'GOrder',
      })
      const recipe = self.client.readFragment<GRecipe.Fragment>({
        id: `Recipe-${recipeId}`,
        fragment: RecipeFragment,
      })
      const optimisticResponse = {
        __typename: 'Mutation',
        createOrderRecipe: {
          ...current,
          countWares: current ? current.countWares + 1 : 1,
          items: [
            ...(current ? current.items : []),
            {
              __typename: 'OrderRecipe',
              id: '-1',
              price: {
                __typename: 'PriceConverted',
                value: 0,
                formattedValue: '---',
              },
              recipe: { ...recipe, sideDishes: [] },
              sideDishes: [],
              automatic: false,
            },
          ],
        },
      }
      await self.client.mutate({
        mutation: AddToOrderMutation,
        variables,
        optimisticResponse,
      })
    },
    async addFromRecipeMobile(recipeId: ID, sideDishes: Array<ID> = []) {
      const variables: GAddToOrder.Variables = {
        orderId: self.orderId!,
        recipeId,
        sideDishes,
      }
      const current = self.client.readFragment<GOrderMobile.Fragment>({
        id: self.fragmentOrderId,
        fragment: OrderMobileFragment,
        fragmentName: 'GOrderMobile',
      })
      const optimisticResponse = {
        __typename: 'Mutation',
        createOrderRecipe: {
          ...current,
          countWares: current ? current.countWares + 1 : 1,
          items: [
            ...(current ? current.items : []),
            {
              __typename: 'OrderRecipe',
              id: '-1',
              recipe: { __typename: 'Recipe', id: recipeId },
              automatic: false,
            },
          ],
        },
      }
      await self.client.mutate({
        mutation: AddToOrderMobileMutation,
        variables,
        optimisticResponse,
      })
    },
    async updateItem(itemId: ID, sideDishes: Array<ID> = []) {
      const variables: GChangeOrderItem.Variables = {
        orderId: self.orderId!,
        itemId,
        sideDishes,
      }
      await self.client.mutate({ mutation: ChangeOrderItemMutation, variables })
    },
    removeItemFromCart(itemId: ID) {
      const variables: GRemoveOrderItem.Variables = {
        itemId,
      }
      const current = self.client.readFragment<GOrder.Fragment>({
        fragment: OrderFragment,
        fragmentName: 'GOrder',
        id: self.fragmentOrderId,
      })
      self.client.mutate({
        mutation: RemoveOrderItemMutation,
        variables,
        optimisticResponse: {
          __typename: 'Mutation',
          destroyOrderRecipe: {
            ...current,
            countWares: current ? current.countWares - 1 : 0,
            items: current
              ? current.items.filter(item => item.id !== itemId)
              : [],
          },
        },
      })
    },
    async initVam() {
      const variables: GVamStatus.Variables = {
        input: {
          orderId: self.orderId!,
          email: self.email,
          firstname: self.firstname,
          lastname: self.lastname,
          phone: self.phone,
        },
      }
      try {
        const result = await self.client.query<GVamStatus.Query>({
          query: VamStatusQuery,
          variables,
        })
        if (result.data) {
          self.useAgreements(result.data.vamStatus)
        }
      } catch (err) {
        reportError(err, { variables })
        self.vamFailed()
      }
    },
    async updateVam(formValues: IOrderFormState) {
      const phone = `${formValues.phonePrefix.trim()}${formValues.phone.trim()}`
      const email = formValues.email.trim()
      const firstname = formValues.firstname.trim()
      const lastname = formValues.lastname.trim()
      const variables: GVamStatus.Variables = {
        input: {
          email,
          phone,
          firstname,
          lastname,
          orderId: self.orderId!,
        },
      }
      try {
        const result = await self.client.query<GVamStatus.Query>({
          query: VamStatusQuery,
          variables,
        })
        if (result.data) {
          self.useAgreements(result.data.vamStatus)
        }
      } catch (err) {
        reportError(err, { variables })
        self.vamFailed()
      }
    },
    submitOrder: async (formValues: IOrderFormState) => {
      self.isSubmitting = true
      let result: FetchResult

      self.rememberFormValues(formValues)

      try {
        result = await self.client.mutate({
          fetchPolicy: 'no-cache',
          mutation: SubmitOrderMutation,
          variables: self.getSubmitVariables(formValues),
          refetchQueries: ['GCartView', 'GMobileCart'],
        })
      } catch (err) {
        self.submitFailed()
        throw reportError(new VError('submit mutation error: %j', err))
      }

      if ((result.errors && result.errors.length) || !result.data) {
        self.submitFailed()
        throw reportError(
          new VError('Submit graphql errors: %j', result.errors),
        )
      }

      const submitResult: GSubmitOrder.FinishOrder = result.data.finishOrder
      if (submitResult.userErrors && submitResult.userErrors.length) {
        self.submitFailed(submitResult.userErrors)
        throw reportError(
          new VError('Order user errors: %j', submitResult.userErrors),
        )
      }

      if (formValues.payment === PaymentGateEnum.Cash) {
        self.submitFinished()
        return
      }

      if (!submitResult.gopayUrl) {
        self.submitFailed()
        throw reportError(new Error('GoPay url missing from response'))
      }

      await executeGoPayPayment(submitResult.gopayUrl)

      self.goPayStatusCheck(true)
      const paymentStatus = await self.queryPaymentStatus()
      self.goPayStatusCheck(false)

      if (paymentStatus && paymentStatus.isSuccess) {
        self.submitFinished()
      } else {
        self.submitFailed(
          (paymentStatus && paymentStatus.userErrors) || undefined,
        )
        throw reportError(new VError('GoPay payment was not successful'))
      }
    },
  }))
  .actions(self => ({
    checkPaymentStatus() {
      const parsedUrl = new window.URL(window.location.href)
      // the orderId will come in URL when GoPay payment was handled by redirect
      const orderId = parsedUrl.searchParams.get('orderId')
      if (!orderId) {
        return
      }
      when(
        () => self.root.auth.isValid,
        async () => {
          // delay displaying status checking modal
          const checkDelay = setTimeout(() => self.goPayStatusCheck(true), 500)
          const paymentStatus = await self.queryPaymentStatus(orderId)

          clearTimeout(checkDelay)
          self.goPayStatusCheck(false)

          if (paymentStatus && paymentStatus.isSuccess) {
            self.orderPaid(orderId)
            parsedUrl.search = ''
            parsedUrl.pathname = '/finish'
            location.replace(parsedUrl.toString())
          } else {
            const phone = self.root.business.branch!.phone
            self.submitFailed([
              i18n.t`We were unable to verify status of your payment. Please contact operator using number ${phone}`,
            ])
            return
          }
        },
      )
    },
  }))

export interface TOrderModel extends Instance<typeof OrderModel> {}
export interface TOrderModelProps extends SnapshotIn<typeof OrderModel> {}
