import gql from 'graphql-tag'

export const CompanyBranchStyleQuery = gql`
  query GCompanyBranchStyle($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      styles {
        active
        main
        mainSecondary
        storeLogo
        storeLogoMobile
        backgroundCart
      }
    }
  }
`
