import gql from 'graphql-tag'

import { JsonAddressFragment } from '../graph/fragments.graphql'

const LocationResolutionFragment = gql`
  fragment GLocationResolution on AddressResolvedLocation {
    finalAddress {
      ...GJsonAddress
    }
    isResolved
    isValidAddress
    inDeliveryLocation
    gpsCoordinates {
      latitude
      longitude
    }
    companyBranch {
      id
      isOpen
    }
  }
  ${JsonAddressFragment}
`

export const ResolveLocationQuery = gql`
  query GResolveLocation(
    $brandId: ID
    $branchId: ID
    $address: String
    $latitude: Float
    $longitude: Float
  ) {
    resolveLocation(
      brand: $brandId
      companyBranch: $branchId
      address: $address
      latitude: $latitude
      longitude: $longitude
    ) {
      ...GLocationResolution
    }
  }
  ${LocationResolutionFragment}
`
