import gql from 'graphql-tag'

import { buildQuery } from '../../graph'
import { PriceFragment } from '../../graph/fragments.graphql'
import { GMobileCart } from '../../graph/types'
import { CartPresenceFragment } from '../foodMenu/FoodMenuView.graphql'

export const OrderMobileFragment = gql`
  fragment GOrderMobile on Order {
    id
    totalSum {
      ...GPrice
    }
    countWares
    ...GCartPresence
  }
  ${PriceFragment}
  ${CartPresenceFragment}
`

export const MobileCartQuery = gql`
  query GMobileCart($orderId: ID!) {
    order(id: $orderId) {
      ...GOrderMobile
    }
  }
  ${OrderMobileFragment}
`

export const QueryMobileCart = buildQuery<
  GMobileCart.Query,
  GMobileCart.Variables
>(MobileCartQuery)
