import {
  applySnapshot,
  getSnapshot,
  Instance,
  SnapshotIn,
  types,
} from 'mobx-state-tree'

import { GraphClient } from '../createClient'
import { logDebug } from '../helpers/logger'
import { persistModel } from '../helpers/persistModel'
import { AuthModel } from './AuthModel'
import { BaseModel } from './BaseModel'
import { BusinessModel } from './BusinessModel'
import { ConfigModel } from './ConfigModel'
import { LocationModel } from './LocationModel'
import { MapModel } from './MapModel'
import { OrderModel } from './OrderModel'
import { UserModel } from './UserModel'

export const RootModel = BaseModel.named('Root')
  .props({
    auth: types.optional(AuthModel, {}),
    business: types.optional(BusinessModel, {}),
    config: types.optional(ConfigModel, {}),
    map: types.optional(MapModel, {}),
    order: types.optional(OrderModel, {}),
    location: types.optional(LocationModel, {}),
    user: types.optional(UserModel, {}),
  })
  .views(self => ({
    get persistedModels() {
      return [
        self.auth,
        self.user,
        self.business,
        self.location,
        self.order,
        self.map,
      ]
    },
  }))
  .actions(self => {
    let initialSnapshot
    return {
      afterCreate() {
        const { business, map, ...snapshot } = getSnapshot(self)
        initialSnapshot = snapshot
      },
      reset() {
        logDebug(initialSnapshot)
        applySnapshot(self, initialSnapshot)
      },
      init: async () => {
        self.auth.attachAuthLinkToClient()
        await Promise.all(self.persistedModels.map(persistModel))
        await Promise.all([
          self.business.queryBusiness(),
          self.order.checkPaymentStatus(),
        ])
        initialSnapshot.business = getSnapshot(self.business)
      },
    }
  })

export interface IRootEnvironment {
  client: GraphClient
  fetch: TFetch
  geolocation: Geolocation
}

export function createRootModel(client: GraphClient) {
  const root = RootModel.create({}, {
    client,
    fetch: window.fetch,
    geolocation: navigator.geolocation,
  } as IRootEnvironment)
  root.init()
  return root
}

export interface TRootModel extends Instance<typeof RootModel> {}
export interface TRootModelProps extends SnapshotIn<typeof RootModel> {}
