import scriptjs from 'scriptjs'

import { logOrder } from './logger'

const embedScript = 'https://gate.gopay.cz/gp-gw/js/embed.js'
const embedScriptSandbox = 'https://gw.sandbox.gopay.com/gp-gw/js/embed.js'

interface ICheckoutResult {
  url: string
  id: number
}

export async function executeGoPayPayment(url: string) {
  logOrder('GoPay init', url)
  await loadScript(url)
  return checkout(getOptions(url))
}

function getOptions(url: string) {
  // const isSafari = /^((?!chrome|android|mobile).)*safari/i.test(
  //   navigator.userAgent,
  // )
  // logOrder('Browser is desktop Safari', isSafari)
  return { gatewayUrl: url, inline: true }
}

function loadScript(url: string) {
  const scriptUrl = url.includes('gw.sandbox.gopay.com')
    ? embedScriptSandbox
    : embedScript
  logOrder('GoPay script url', scriptUrl)
  return new Promise(resolve => {
    scriptjs(scriptUrl, resolve)
  })
}

function checkout(options: object): Promise<number> {
  return new Promise(resolve => {
    logOrder('GoPay checkout')
    // @ts-ignore
    _gopay.checkout(options, function(result: ICheckoutResult) {
      logOrder('GoPay result', result)
      resolve(result.id)
    })
  })
}
