import './App.css'

import { Trans } from '@lingui/react'
import React from 'react'
import styled from 'styled-components'

import { ErrorContainer } from '../common/ErrorContainer'
import { PageTitle } from '../common/PageTitle'
import { Desktop, Mobile, MobileLandscape } from '../common/Responsive'
import { ResetPersistenceButton } from './ResetPersistenceButton'

const LandscapeMessage = styled.div`
  font-size: 1.1rem;
  padding: 3rem;
  vertical-align: center;
`

const DesktopApp = React.lazy(() => import('./DesktopApp'))
const MobileApp = React.lazy(() => import('./MobileApp'))

const App = () => (
  <ErrorContainer prod={process.env.NODE_ENV === 'production'}>
    <PageTitle />
    <Desktop>
      <DesktopApp />
    </Desktop>
    <Mobile>
      <MobileApp />
    </Mobile>
    <MobileLandscape>
      <LandscapeMessage>
        <Trans>
          We know, you like space. Our app is optimised for desktop. Leave your
          phone in portrait for now.
        </Trans>
      </LandscapeMessage>
    </MobileLandscape>
    <ResetPersistenceButton />
  </ErrorContainer>
)

export default App
