import { DocumentNode } from 'graphql'
import React from 'react'
import { Query, QueryProps, QueryResult } from 'react-apollo'
import RedBox from 'redbox-react'
import { VError } from 'verror'

import Pacman from '../common/Pacman'
import { logError } from '../helpers/logger'
import { reportError } from '../helpers/sentry'

export function buildQuery<TData, TVariables>(query: DocumentNode) {
  type TResult = QueryResult<TData, TVariables>
  type TRenderProp<T> = (data: TData, result: TResult) => T
  interface IProps
    extends Omit<QueryProps<TData, TVariables>, 'query' | 'children'> {
    render?: TRenderProp<ReactNode>
    renderFragment?: TRenderProp<ReactNode[]>
    renderLoading?: boolean | ((result: TResult) => ReactNode)
    renderError?: boolean | ((result: TResult) => ReactNode)
  }
  const GQuery: React.SFC<IProps> = ({
    render,
    renderFragment,
    renderLoading = true,
    renderError = true,
    children,
    ...props
  }) => (
    <Query query={query} {...props}>
      {result => {
        if (result.loading || !result.data) {
          if (typeof renderLoading === 'function') {
            return renderLoading(result)
          } else if (renderLoading === true) {
            return <Pacman />
          }
        }
        if (result.error) {
          reportError(result.error)
          logError(result.error)
          if (typeof renderError === 'function') {
            return renderError(result)
          } else if (renderError === true) {
            if (process.env.NODE_ENV === 'production') {
              throw new VError(result.error, 'Query error')
            }
            return <RedBox error={result.error} />
          } else {
            return null
          }
        }
        if (result.data) {
          if (renderFragment) {
            return <>{renderFragment(result.data, result)}</>
          }
          if (render) {
            return render(result.data, result)
          }
        }
        return children || null
      }}
    </Query>
  )

  return GQuery
}
