import gql from 'graphql-tag'

import { buildQuery } from '../../graph'
import { CartItemFragment, PriceFragment } from '../../graph/fragments.graphql'
import { GCartView } from '../../graph/types'

export const OrderFragment = gql`
  fragment GOrder on Order {
    id
    countWares
    items {
      ...GCartItem
      price: priceWithSideDishes {
        ...GPrice
      }
      sideDishes {
        ...GCartItem
      }
    }
    coverPrice {
      ...GPrice
    }
    transportFee {
      ...GPrice
    }
    discounts {
      code
      description
      price {
        ...GPrice
      }
    }
    totalDiscount {
      ...GPrice
    }
    totalSum {
      ...GPrice
    }
  }
  ${CartItemFragment}
  ${PriceFragment}
`

export const CartViewQuery = gql`
  query GCartView($orderId: ID!) {
    order(id: $orderId) {
      ...GOrder
      deliveryType {
        enum
      }
    }
  }
  ${OrderFragment}
`

export const QueryCartView = buildQuery<GCartView.Query, GCartView.Variables>(
  CartViewQuery,
)
