import gql from 'graphql-tag'

export const CompanyBranchFragment = gql`
  fragment GCompanyBranch on CompanyBranch {
    id
    code: wac
    brand {
      id
      name
      code: wac
    }
    company {
      id
    }
    address {
      country {
        phonePrefix
      }
    }
    phone
  }
`

export const BranchLookupQuery = gql`
  query GBusinessCodeLookup($code: String, $url: String) {
    lookup: companyBranchLookUp(wac: $code, hostUrl: $url) {
      branch: companyBranch {
        ...GCompanyBranch
      }
      isBrand: isBrandWac
      isMobileApp
      knownBasePath
      orderOrigin
    }
  }
  ${CompanyBranchFragment}
`
