import { GGeoPoint } from '../graph/types'

export type GeoPoint = Omit<GGeoPoint.Fragment, '__typename'>

export const gpsToLatLng = (gps: GeoPoint): google.maps.LatLngLiteral => ({
  lat: Number(gps.latitude),
  lng: Number(gps.longitude),
})

export const latLngToGps = (latLng: google.maps.LatLng): GeoPoint => ({
  latitude: Number(latLng.lat()),
  longitude: Number(latLng.lng()),
})
