import { Observer } from 'mobx-react'
import React, { useContext } from 'react'

import { TRootModel } from '../models/RootModel'

export const rootContext = React.createContext<TRootModel | undefined>(
  undefined,
)

interface IProps {
  render(root: TRootModel): ReactNode
  observe?: boolean
}

export const useRoot = () => {
  const root = useContext(rootContext)
  if (!root) {
    throw new Error('RootProvider missing in the tree')
  }
  return root
}

export const WithRoot: React.FC<IProps> = ({ render, observe = false }) => {
  const root = useRoot()
  if (observe === true) {
    return <Observer>{() => render(root)}</Observer>
  }
  return render(root)
}

export const RootProvider = rootContext.Provider
