import { Instance, SnapshotIn, types } from 'mobx-state-tree'

import { appConfig } from '../config'
import {
  GBusinessCodeLookup,
  GCompanyBranch,
  OrderOriginEnum,
} from '../graph/types'
import { logMain } from '../helpers/logger'
import { setSentryContext } from '../helpers/sentry'
import { BannerModel, TBannerModel } from './BannerModel'
import { BaseModel } from './BaseModel'
import { BranchLookupQuery } from './BusinessModel.graphql'
import { ThemeModel } from './ThemeModel'

type TBranch = Omit<GCompanyBranch.Fragment, 'address'>

interface IVolatileProps {
  isBrand: boolean
  isMobileApp: boolean
  branch: Maybe<TBranch>
  country: Maybe<GCompanyBranch.Country>
  orderOrigin: OrderOriginEnum
  routerBasePath: string
}

export const BusinessModel = BaseModel.named('Business')
  .props({
    code: types.maybeNull(types.string),
    theme: types.optional(ThemeModel, {}),
    banners: types.map(BannerModel),
  })
  .volatile<IVolatileProps>(() => ({
    isBrand: false,
    isMobileApp: false,
    branch: null,
    country: null,
    orderOrigin: OrderOriginEnum.Web,
    routerBasePath: '',
  }))
  .views(self => ({
    get branchId() {
      return self.branch && self.branch.id
    },
    get companyId() {
      return self.branch && self.branch.company.id
    },
    get brandId() {
      return self.branch && self.branch.brand.id
    },
    get isValid() {
      return self.branch !== null
    },
    get requestedCode() {
      // app can be built with a specific code for a purpose of mobile apps
      let code = process.env.REACT_APP_BUSINESS_CODE || null
      if (!code) {
        // mostly used in DEV because it's not possible to use subdomains for
        // business resolution ... use either code from url or stored one as fallback
        const urlCode = readBusinessCodeFromUrl()
        code = urlCode || self.code
      }
      return code
    },
  }))
  .actions(self => ({
    updateFromLookup(lookup: GBusinessCodeLookup.Lookup) {
      const {
        isBrand,
        isMobileApp,
        knownBasePath,
        orderOrigin,
        branch,
      } = lookup

      if (!branch) {
        return
      }

      self.isBrand = isBrand
      self.isMobileApp = isMobileApp
      self.branch = branch
      self.country = branch.address.country || null
      self.routerBasePath = knownBasePath
      self.orderOrigin = orderOrigin

      if (branch.id !== self.root.order.branchId) {
        self.root.order.orderCancel()
      }

      if (process.env.REACT_APP_STAGE !== 'prod') {
        // for non-prod enviroments we need to remember business code
        // because its lost on redirects followed by a reload
        self.code = isBrand ? self.branch.brand.code : self.branch.code
      } else {
        self.code = null
      }
    },
    mockBranchData(branch: TBranch) {
      self.branch = branch
    },
  }))
  .actions(self => ({
    queryBusiness: async () => {
      const { data } = await self.client.query<GBusinessCodeLookup.Query>({
        query: BranchLookupQuery,
        variables: {
          code: self.requestedCode,
          url: window.location.href,
        },
      })

      if (!(data && data.lookup && data.lookup.branch)) {
        logMain('no business found, used code %s', self.requestedCode)
        redirectAway()
        return
      }

      self.updateFromLookup(data.lookup)

      if (self.branchId) {
        const stylesQuery = self.theme.queryStyles(self.branchId)
        // no need to wait for styles if some were loaded previously
        if (!self.theme.loaded) {
          await stylesQuery
        }
      }

      setSentryContext({
        requestedCode: self.requestedCode,
        branchId: self.branchId,
        brandId: self.brandId,
        companyId: self.companyId,
        isBrand: self.isBrand,
      })

      logMain(
        'identified branch %s, brand %s; company %s, isBrand: %s',
        self.branchId,
        self.brandId,
        self.companyId,
        self.isBrand,
      )
    },
    ensureBannerForCurrentBranch(): TBannerModel {
      const branchId = self.branchId!
      if (!self.banners.has(branchId)) {
        self.banners.set(branchId, { branchId: branchId })
      }
      return self.banners.get(branchId)!
    },
  }))

const codeParam = 'wac'

function readBusinessCodeFromUrl() {
  const parsedUrl = new window.URL(window.location.href)
  return parsedUrl.searchParams.get(codeParam) || null
}

function redirectAway() {
  if (process.env.NODE_ENV === 'production') {
    window.location.replace(appConfig.redirectOnFail)
  } else {
    document.write('No business identified')
  }
}

export interface TBusinessModel extends Instance<typeof BusinessModel> {}
export interface TBusinessModelProps extends SnapshotIn<typeof BusinessModel> {}
