import { InMemoryCache, NormalizedCacheObject } from 'apollo-cache-inmemory'
import { ApolloClient } from 'apollo-client'
import { ApolloLink } from 'apollo-link'
import { BatchHttpLink } from 'apollo-link-batch-http'
import createLogLink from 'apollo-link-log'
import QueueLink from 'apollo-link-queue'
import { RetryLink } from 'apollo-link-retry'
import { autorun } from 'mobx'

import { appConfig } from './config'
import { createErrorLink } from './graph/errorHandler'
import { logGraph } from './helpers/logger'
import { isNetworkOnline } from './helpers/offline'

export type GraphClient = ApolloClient<NormalizedCacheObject>

export function createApolloClient() {
  const cache = new InMemoryCache({
    dataIdFromObject: (o: { __typename?: string; id?: string }) => {
      if (o.__typename != null && o.id != null) {
        return `${o.__typename}-${o.id}`
      }
      return null
    },
  })

  const loggingLink = createLoggingLink()
  const httpLink = createHttpLink()
  const retryLink = createRetryLink()
  const offlineLink = createOfflineLink()
  const errorLink = createErrorLink()

  const link = ApolloLink.from([
    loggingLink,
    retryLink,
    offlineLink,
    errorLink,
    httpLink,
  ])

  return new ApolloClient({ link, cache })
}

function createHttpLink() {
  return new BatchHttpLink({
    uri: `${appConfig.endpoint}/graphql`,
    credentials: 'same-origin',
  })
}

function createLoggingLink() {
  return createLogLink({
    enabled: process.env.REACT_APP_STAGE === 'dev',
  })
}

function createRetryLink() {
  return new RetryLink({
    delay: {
      initial: 300,
      max: Infinity,
      jitter: true,
    },
    attempts: {
      max: 5,
      retryIf: (error, _operation) => !!error,
    },
  })
}

function createOfflineLink() {
  const offlineLink = new QueueLink()
  autorun(() => {
    if (isNetworkOnline()) {
      logGraph('client is online')
      offlineLink.open()
    } else {
      logGraph('client is offline')
      offlineLink.close()
    }
  })
  return offlineLink
}
