import gql from 'graphql-tag'

import { buildQuery } from '../../graph/buildQuery'
import { PriceFragment } from '../../graph/fragments.graphql'
import { GFoodMenu, GFoodMenuCartPresence } from '../../graph/types'

export const FoodMenuQuery = gql`
  query GFoodMenu($branchId: ID!) {
    wareCategories(
      wareCategoryType: MAIN
      companyBranch: $branchId
      enabled: true
    ) {
      ...GFoodMenuCategory
      recipes(enabled: true, isAvailable: true) {
        ...GFoodMenuRecipe
      }
    }
  }

  fragment GFoodMenuCategory on WareCategory {
    id
    nameLabel
    pictogram
  }

  fragment GFoodMenuRecipe on Recipe {
    id
    image(width: 192, height: 192)
    nameLabel
    descriptionLabel
    price {
      ...GPrice
    }
    sideDishCount: sideDishesWareCategoriesCount
  }

  ${PriceFragment}
`

export const CartPresenceFragment = gql`
  fragment GCartPresence on Order {
    items {
      id
      recipe {
        id
      }
    }
  }
`

export const FoodMenuCartPresenceQuery = gql`
  query GFoodMenuCartPresence($orderId: ID!) {
    order(id: $orderId) {
      id
      ...GCartPresence
    }
  }
  ${CartPresenceFragment}
`

export const QueryFoodMenuCartPresence = buildQuery<
  GFoodMenuCartPresence.Query,
  GFoodMenuCartPresence.Variables
>(FoodMenuCartPresenceQuery)

export const QueryFoodMenu = buildQuery<GFoodMenu.Query, GFoodMenu.Variables>(
  FoodMenuQuery,
)
