import { Catalog, setupI18n } from '@lingui/core'
import { I18nProvider } from '@lingui/react'
import { useAsyncEffect } from '@speedlo/hooks'
import * as detector from 'locales-detector'
import React, { useState } from 'react'

import Pacman from './common/Pacman'
import { appConfig } from './config'
import { logLang } from './helpers/logger'

export const i18n = setupI18n()

export const I18nManager: React.FC = ({ children }) => {
  const [isReady, setReady] = useState(false)
  const [language] = useState(detectLanguage)

  useAsyncEffect(async () => {
    await changeLanguage(language)
    setReady(true)
  })

  if (isReady === false) {
    return <Pacman />
  }

  return (
    <I18nProvider language={language} i18n={i18n}>
      {children}
    </I18nProvider>
  )
}

export async function changeLanguage(lang: string) {
  if (!appConfig.supportedLanguages.includes(lang)) {
    logLang('language %s is not supported')
    return false
  }
  await loadCatalog(lang)
  i18n.activate(lang)
  logLang('changed to %s', lang)
  return true
}

export function detectLanguage() {
  const resolver = new detector.LocaleResolver(
    [new detector.UrlDetector('lang'), new detector.NavigatorDetector()],
    [new detector.LanguageOnlyTransformer()],
  )
  const languages: ReadonlyArray<string> = resolver.getLocales()
  logLang('detected languages %s', languages.join('; '))
  const supported = languages.find(loc =>
    appConfig.supportedLanguages.includes(loc),
  )
  return supported || appConfig.fallbackLanguage
}

async function loadCatalog(lang: string) {
  let catalog: Catalog
  if (process.env.NODE_ENV !== 'production') {
    // prettier-ignore
    catalog = await import(
      /* webpackMode: "lazy", webpackChunkName: "i18n-[index]" */
      `@lingui/loader!./locale/${lang}/messages.po`
    )
  } else {
    // prettier-ignore
    catalog = await import(
      /* webpackMode: "lazy", webpackChunkName: "i18n-[index]" */
      `./locale/${lang}/messages.js`
    )
  }
  i18n.load({ [lang]: catalog })
  logLang('loaded catalog %s', lang)
}
