import gql from 'graphql-tag'

import { PriceFragment } from '../graph/fragments.graphql'
import { OrderFragment } from '../segments/cart/CartView.graphql'
import { OrderMobileFragment } from '../segments/mobile/MobileCart.graphql'

const CartRecipeFragment = gql`
  fragment GCartRecipe on Recipe {
    id
    nameLabel
    descriptionLabel
    categories: sideDishesWareCategories {
      id
      nameLabel
      minCount
      maxCount
      sideDishes {
        id
        nameLabel
        price {
          ...GPrice
        }
      }
    }
  }
  ${PriceFragment}
`

export const OrderCartForRecipeQuery = gql`
  query GOrderCartForRecipe($recipeId: ID!) {
    recipe(id: $recipeId) {
      ...GCartRecipe
    }
  }
  ${CartRecipeFragment}
`

export const OrderCartForItemQuery = gql`
  query GOrderCartForItem($itemId: ID!) {
    item: orderRecipe(id: $itemId) {
      id
      recipe {
        ...GCartRecipe
      }
      sideDishes {
        id
        recipe {
          id
        }
      }
    }
  }
  ${CartRecipeFragment}
`

const CreateOrderFragment = gql`
  fragment GCreateOrder on Order {
    id
    companyBranch {
      id
    }
  }
`

export const CreatePickupOrderMutation = gql`
  mutation GCreatePickupOrder($branchId: ID!, $orderOrigin: OrderOriginEnum!) {
    order: createPickupOrder(
      order: { companyBranch: $branchId, orderOrigin: $orderOrigin }
    ) {
      ...GCreateOrder
    }
  }
  ${CreateOrderFragment}
`

export const CreateDeliveryOrderMutation = gql`
  mutation GCreateDeliveryOrder(
    $brandId: ID
    $branchId: ID
    $gps: InputGeoPointCreate!
    $orderOrigin: OrderOriginEnum!
  ) {
    order: createDeliveryOrder(
      order: {
        brand: $brandId
        companyBranch: $branchId
        gps: $gps
        orderOrigin: $orderOrigin
      }
    ) {
      ...GCreateOrder
    }
  }
  ${CreateOrderFragment}
`

export const AddToOrderMutation = gql`
  mutation GAddToOrder($orderId: ID!, $recipeId: ID!, $sideDishes: [ID!]) {
    createOrderRecipe(
      orderRecipe: {
        order: $orderId
        recipe: $recipeId
        sideDishes: $sideDishes
      }
    ) {
      ...GOrder
    }
  }
  ${OrderFragment}
`

export const AddToOrderMobileMutation = gql`
  mutation GAddToOrderMobile(
    $orderId: ID!
    $recipeId: ID!
    $sideDishes: [ID!]
  ) {
    createOrderRecipe(
      orderRecipe: {
        order: $orderId
        recipe: $recipeId
        sideDishes: $sideDishes
      }
    ) {
      ...GOrderMobile
    }
  }
  ${OrderMobileFragment}
`

export const ChangeOrderItemMutation = gql`
  mutation GChangeOrderItem($orderId: ID!, $itemId: ID!, $sideDishes: [ID!]) {
    updateOrderRecipe(
      id: $itemId
      orderRecipe: { order: $orderId, sideDishes: $sideDishes }
    ) {
      ...GOrder
    }
  }
  ${OrderFragment}
`

export const RemoveOrderItemMutation = gql`
  mutation GRemoveOrderItem($itemId: ID!) {
    destroyOrderRecipe(id: $itemId) {
      ...GOrder
    }
  }
  ${OrderFragment}
`

export const SubmitOrderMutation = gql`
  mutation GSubmitOrder(
    $orderId: ID!
    $customer: InputCustomerCreate
    $note: String
    $paymentGate: PaymentGateEnum!
    $deliverAt: DateTime!
    $agreements: [ID!]
  ) {
    finishOrder(
      id: $orderId
      order: {
        confirmedAgreements: $agreements
        customer: $customer
        paymentGate: $paymentGate
        note: $note
        deliverAt: $deliverAt
      }
    ) {
      userErrors
      gopayUrl
    }
  }
`

export const OrderPaymentStatusQuery = gql`
  query GOrderPaymentStatus($orderId: ID!) {
    status: orderOnlinePaymentStatus(id: $orderId) {
      isSuccess
      userErrors
    }
  }
`

export const VamStatusQuery = gql`
  query GVamStatus($input: InputVamStatus!) {
    vamStatus(input: $input) {
      id
      name
      description
      isConfirmed
      isMandatory
    }
  }
`
